  ionViewDidLoad() {
    console.log('ionViewDidLoad MySetServiceSerectPage');
  }
  ionViewDidEnter() {

    this.addMySetServiceSerectPage();
  }
  send(value, isshow?: boolean) {
    if (isshow) {
      this.loader = this.loadingCtrl.create({
        message: "加载中...",
        duration: 3000
      });
      this.loader.present();
    }

    this.appMin.post(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);
    })


  }

  addMySetServiceSerectPage() {
    //加载初始数据
    let obj = {
      type: "addMySetServiceSerectPage",
      userId: this.appMin.getUserid(),
    }

    this.send(JSON.stringify(obj), false);
  }
  serviceBtn(obj) {
    WitePrompt("send serviceId is " + obj);
    let par = {
      serviceId: obj
    }
    // this.navCtrl.push(MyServiceSetPage, par);
    //查看按钮单击事件
    //跳转
    this.Router.navigate(['my-service-info'],{queryParams:par});

  }
  addservice_click() {
    //添加新服务

    //  this.navCtrl.push(CreatServicePage);
    this.Router.navigate(['my-set-service-create'], { queryParams: {} });


  }
  deleteService(serviceId) {
    //删除服务
    this.showConfirm(serviceId);


  }
  async  showConfirm(serviceId) {
    let confirm = await this.alertCtrl.create({
      header: '删除咨询项目?',
      message: '你确定要删除咨询项目吗?',
      buttons: [
        {
          text: '取消',
          handler: () => {
            console.log('Disagree clicked');
          }
        },
        {
          text: '删除',
          handler: () => {
            console.log('Agree clicked');
            let obj = {
              type: "deleteService",
              serviceId: serviceId,
              userId: this.appMin.getUserid()
            }

            this.send(JSON.stringify(obj), true);
          }
        }
      ]
    });
    confirm.present();
  }
  dataHandle(meg: any) {

    if (meg['type'] == "addMySetServiceSerectPage_ret") {
      this.objlist = meg["rows"];

    }
    if (meg['type'] == "deleteService_ret") {
      //删除完成 重新加载数据
      this.addMySetServiceSerectPage();
    }
  }


  ngOnInit() {
  }

